  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

*:focus,
*:active {
  outline: none; }

a:focus,
a:active {
  outline: none; }

nav,
footer,
header,
aside {
  display: block; }

html,
body {
  height: 100%;
